<template>
  <div id="consumer-detail">
    <title-bar title="" @refresh="refresh"></title-bar>
    <search-group :searchList="searchList" @search="search"></search-group>
    <table-container @on-change="pageChange" @on-page-size-change="pageSizeChange" page :pageprops="pageprops">
      <div slot="btn">
        {{Math.abs(all_price)}}
      </div>
      <Table :columns="columns" :data="myData" border :loading="tableLoading"></Table>
    </table-container>
  </div>
</template>
<script>
export default {
  name: "consumer-detail",
  data() {
    return {
      all_price: '',
      columns: [
        {
          title: '',
          key: 'nick_name',
          align: 'center'
        },{
          title: 'id',
          key: 'uuid',
          align: 'center'
        }, {
          title: '',
          key: 'mobile',
          align: 'center'
        }, {
          title: '',
          key: 'remark',
          align: 'center'
        }, {
          title: '',
          key: 'price',
          align: 'center'
        }, {
          title: '',
          key: 'created_at',
          align: 'center'
        }, {
          title: '',
          key: 'balance',
          align: 'center'
        }
      ],
      myData: [],
      tableLoading: false,
      searchList: [
        {
          label: '',
          type: 'input',
          placeholder: '',
          model: 'nick_name'
        },{
          label: 'id',
          type: 'input',
          placeholder: 'id',
          model: 'uuid'
        },{
          label: '',
          type: 'input',
          placeholder: '',
          model: 'mobile'
        },{
          label: '',
          type: 'daterange',
          placeholder: '',
          model: 'created_time',
          start_end: ['start_time','end_time']
        }
      ],
      pageprops: { //
        showSizer:true,
        total:0,
      },
      fy: { //
        page: 1,
        size: 10
      },
      searchForm: {}, //
      my_search: {
        pay_type: 1
      }
    }
  },
  computed: {
    searchData () {
      return Object.assign(this.fy,this.searchForm,this.my_search);
    }
  },
  methods: {
    refresh() {
      this.getData();
    },
    search(data) {
      this.searchForm = data;
      this.getData();
    },
    pageChange(page) {
      this.fy.page = page;
      this.getData();
    },
    pageSizeChange(size) {
      this.fy.size = size;
      this.getData();
    },
    getData() {
      this.tableLoading = true
      this.axios.get('bill-user-log-list',{
        params:this.searchData
      }).then(res=>{
        if(res){
          this.tableLoading = false
          this.all_price = res.data.all_price
          this.myData = res.data.log_list
        }
      })
    },
  },
  mounted() {
    this.getData();
  }
}
</script>
<style lang="scss" scoped>
</style>



