<template>
  <div id="userMsgBtn">
    <!--  -->
    <Button type="ghost" @click="duanxin_modal=true"></Button>
    <Button type="success" @click="zn_modal=true"></Button>
    <Button type="error" @click="app_modal=true">APP</Button>
    <Button type="warning" @click="money_modal=true"></Button>
    <Button type="info"></Button>
    <Modal v-model="duanxin_modal" title="">
      <div class="duanxin-container">
        <Form ref="duanxin_form" :model="duanxin_form" :rules="duanxin_rule" :label-width="80">
          <FormItem label=":">
            <span style="color:red">{{duanxin_form.send_type==''?select.length:total}}</span>
          </FormItem>
          <FormItem label=":">
            <RadioGroup v-model="duanxin_form.send_type">
              <Radio label=""></Radio>
              <Radio label=""></Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label=":" prop="content">
            <Input type="textarea" :maxlength="500" v-model="duanxin_form.content"></Input>
          </FormItem>
          <FormItem>
            <div>
              
            </div>
            <div>
              <span class="red">500</span><span class="red">3</span><span class="red">1</span>
            </div>
          </FormItem>
          <FormItem label="">
            <span style="margin-right:10px;"><span class="red">20</span></span> <span><span class="red">100000</span> </span>
          </FormItem>
        </Form>
      </div>
      <div slot="footer">
        <Button></Button>
        <Button type="primary" @click="duanxin_submit" :loading="duanxin_modal_loading"></Button>
      </div>
    </Modal>

    <!--  -->
    <Modal v-model="zn_modal" title="">
      <div class="duanxin-container">
        <Form ref="zn_form" :model="zn_form" :rules="zn_rule" :label-width="80">
          <FormItem label=":">
            <span style="color:red">{{zn_form.send_type==''?select.length:total}}</span>
          </FormItem>
          <FormItem label=":">
            <RadioGroup v-model="zn_form.send_type">
              <Radio label=""></Radio>
              <Radio label=""></Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label=":" prop="title">
            <Input v-model="zn_form.title" :maxlength="20"></Input>
          </FormItem>
          <FormItem label=":" prop="content">
            <Input type="textarea" :maxlength="100" v-model="duanxin_form.content"></Input>
          </FormItem>
          <FormItem>
            <div>
              <span class="red">20</span><span class="red">100</span>
            </div>
          </FormItem>
        </Form>
      </div>
      <div slot="footer">
        <Button></Button>
        <Button type="primary" @click="zn_submit" :loading="zn_modal_loading"></Button>
      </div>
    </Modal>

    <!-- APP -->
    <Modal v-model="app_modal" title="APP">
      <div class="app-container">
        <Form ref="app_form" :model="app_form" :rules="app_rule" :label-width="80">
          <FormItem label=":">
            <span style="color:red">{{app_form.send_type==''?select.length:total}}</span>
          </FormItem>
          <FormItem label=":">
            <RadioGroup v-model="app_form.send_type">
              <Radio label=""></Radio>
              <Radio label=""></Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label=":">
            <RadioGroup v-model="app_form.push_type">
              <Radio label=""></Radio>
              <Radio label=""></Radio>
              <Radio label=""></Radio>
              <Radio label=""></Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label=":" prop="url" v-if="app_form.push_type == ''">
            <Input v-model="app_form.url"></Input>
          </FormItem>
          <FormItem :label="''+app_form.push_type+':'" prop="choose" v-if="app_form.push_type != ''">
            <div class="chose-btn">

            </div>
          </FormItem>
          <FormItem label=":" prop="title">
            <Input v-model="app_form.title" :maxlength="20"></Input>
          </FormItem>
          <FormItem label=":" prop="content">
            <Input :maxlength="100" v-model="app_form.content"></Input>
          </FormItem>
        </Form>
      </div>
      <div slot="footer">
        <Button></Button>
        <Button type="primary" @click="zn_submit" :loading="zn_modal_loading"></Button>
      </div>
    </Modal>

    <Modal v-model="money_modal" title="">
      <Form ref="money_form" :model="money_form" :rules="money_rule" :label-width="80">
        <FormItem label=":">
          <span style="color:red">{{money_form.send_type==''?select.length:total}}</span>
        </FormItem>
        <FormItem label=":">
          <RadioGroup v-model="money_form.send_type">
            <Radio label=""></Radio>
            <Radio label=""></Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label=":">
          <Input v-model="app_form.url"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button></Button>
        <Button type="primary" @click="money_submit" :loading="money_modal_loading"></Button>
      </div>
    </Modal>
  </div>
</template>
<script>
export default {
  name: "userMsgBtn",
  props: {
    select: {
      type: Array
    },
    total: {
      type: [String,Number]
    }
  },
  data: () => ({
    //
    money_modal: false,
    money_form: {
      send_type: '',
      num: ''
    },
    money_modal_loading:false,
    money_rule: {
      money: [{
        required: true,
        message: '',
        trigger: 'blur'
      }],
    },


    // 
    duanxin_modal: false,
    duanxin_form: {
      send_type: '',
      content: ''
    },
    duanxin_rule: {
      content: [{
        required: true,
        message: '',
        trigger: 'blur'
      }],
    },
    duanxin_modal_loading: false,

    // 
    zn_modal: false,
    zn_form: {
      send_type: '',
      title: '',
      content: '',
    },
    zn_rule: {
      content: [{
        required: true,
        message: '',
        trigger: 'blur'
      }],
      title: [{
        required: true,
        message: '',
        trigger: 'blur'
      }],
    },
    zn_modal_loading: false,

    // APP
    app_modal: false,
    app_form: {
      send_type: '',
      push_type: '',
      url: ''
    },
    app_rule: {
      content: [{
        required: true,
        message: '',
        trigger: 'blur'
      }],
      title: [{
        required: true,
        message: '',
        trigger: 'blur'
      }],
      url: [{
        required: true,
        message: '',
        trigger: 'blur'
      }],
      choose: [{
        required: true,
        message: '',
        trigger: 'blur'
      }]
    },
    app_modal_loading: false,
  }),
  methods: {
    duanxin_submit() {
      this.$refs.duanxin_form.validate((valid) => {
        if (valid) {
          this.duanxin_modal_loading = true
          setTimeout(() => {
            this.duanxin_modal_loading = false
            this.duanxin_modal = false
          }, 2000)
        }
      })
    },
    zn_submit() {

    },
    money_submit() {}
  }
}
</script>
<style lang="scss" scoped>
.red {
    color: red;
}
.app-container{
  .chose-btn{
    height:33px;
    background:#eee;
    border-radius:5px;
    cursor: pointer;
  }
}
</style>



