<template>
<div id="user">
  <title-bar title="" @refresh="refresh">

  </title-bar>
  <search-group :searchList="searchList" @search="search">
  </search-group>
  <table-container @on-change="pageChange" @on-page-size-change="pageSizeChange" page :pageprops="pageprops">
    <Table :columns="columns" :data="myData" border :loading="tableLoading" @on-selection-change="select"></Table>
  </table-container>

  <user-detail ref="userDetail"></user-detail>

</div>
</template>
<script>
import msgBtn from './components/user-msg-btn.vue'
import userDetail from './components/user-detail.vue'
export default {
  name: "user",
  data (){
    return {
      select_arr: [], //
      // 
      senior_search: false,  //
      tableLoading: false, //

      // user_detail_show: false,

      columns: [
        {
          title: 'ID',
          key: 'id',
          align: 'center'
        }, {
          title: '',
          key: 'nickname',
          align: 'center'
        },{
          title: '',
          key: 'gender',
          align: 'center',
          render: (h,params)=>{
            let gender;
            switch(params.row.gender) {
              case 1:gender = '';break;
              case 2:gender = '';break;
            }
            return h('span',gender)
          }
        }, {
          title: '',
          key: 'province',
          align: 'center'
        }, {
          title: '',
          key: 'city',
          align: 'center'
        },{
          title: '',
          key: 'avatarulr',
          width: '80',
          align:'center',
          render: (h,params)=>{
            return h('img',{
              attrs: {
                src: params.row.avatarulr
              },
              style: {
                width: "45px",
                height: "45px",
                marginTop: "5px"
              }
            })
          }
        }, {
          title: '',
          key: 'created_at',
          align: 'center'
        }, {
          title: '',
          key: 'operation',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'info'
                },
                on: {
                  click: () => {
                    this.$refs.userDetail.show(params.row.uuid)
                  }
                }
              }, '')
            ])
          }
        }
      ], //

      myData: [], //

      searchList: [{   //
        label: '',
        type: 'input',
        placeholder: '',
        model: 'keyword'
      },{
        label: '',
        type: 'select',
        placeholder: '',
        options: [{
          label: '',
          value: '',
        },{
          label: '',
          value: 1
        },{
          label: '',
          value: 2
        }],
        model: 'sex'
      },{
        label: '',
        type: 'daterange',
        placeholder: '',
        model: 'register_time',
        start_end: ['start_time','end_time']
      }],

      pageprops: { //
        showSizer:true,
        total:0,
      },
      fy: { //
        page: 1,
        size: 10
      },
      searchForm: {}, //
      my_search: {} //
    }
  },
  computed: {
    searchData () {
      return Object.assign(this.fy,this.searchForm,this.my_search);
    }
  },
  methods: {
    select(selection) {
      this.select_arr = selection
    },
    search(data) {
      this.searchForm = data;
      this.getData();
    },
    refresh() {
      console.log('')
    },
    pageChange(page) {
      this.fy.page = page;
      this.getData();
    },
    pageSizeChange(size) {
      this.fy.page = 1;
      this.fy.size = size;
      this.getData();
    },
    getData () {
      this.tableLoading = true;
      this.axios.get(`user-list`,{
        params: this.searchData
      }).then(res => {
        this.tableLoading = false;
        if(res) {
          this.myData = res.data.list;
          this.pageprops.total = res.data.total;
        }
      })
    }
  },
  mounted() {
    this.getData();
  },
  components: {
    msgBtn,
    userDetail
  }
}
</script>
<style lang="scss" scoped>
.red {
    color: red;
}
.senior-search-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    > div {
        width: 40%;
        margin-bottom: 10px;
        .label {
            white-space: nowrap;
            margin-bottom: 10px;
            font-size: 14px;
        }
    }
}
.app-container{
  .chose-btn{
    height:33px;
    background:#eee;
    border-radius:5px;
    cursor: pointer;
  }
}
  .avatar-img{
    width: 120px;
    height: 120px;
  }
</style>



